  top: 50%;
  right: 4px;
  display: inline-block;
  width: 15px;
  height: 2px;
  margin-top: -1px;
  content: "";
  background-color: #fff;
  -webkit-transition: all 0.6s ease;
  -o-transition: all 0.6s ease;
  transition: all 0.6s ease;
